@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@300;400;700&display=swap);
body {
    margin: 0;
    font-family: 'Nunito', sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    font-size: 14px;
    color: white;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

* {
    box-sizing: border-box;
}

@media screen and (min-width: 48em) {
    body {
        font-size: 16px;
    }
}
.App {
  text-align: center;
}

.logo-wrapper {
    margin-bottom: 2em;
}

.logo {
    width: 30em;
    max-width: 100%;
    margin: 0 auto;
    background: white;
    color: grey;
    overflow: auto;
    border-radius: .2em;
}

    .logo__title {
        text-align: center;
        padding: 1em;
        margin: 0;
    }

    .logo__text {
        margin: 3em 0;
        text-align: center;
    }
.logos {
}
.container__background {
    position: fixed;
    z-index: -1;
    left: 0;
    top: 0;
    width: 100vw;
    height: 100vh;
    transition: background-color 0.3s ease-in-out;
}

.container__background--primary {
    background: rgb(102, 168, 255);
}

.container__background--secondary {
    background: rgb(255, 90, 90);
}

.menu-wrapper {
    display: flex;
    align-items: center;
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    padding: 1em 2em;
    background: rgba(120, 120, 120, .6);
    justify-content: space-between;
    z-index: 2;
}

.header-logo {
    float: left;
    border-radius: 50%;
    width: 3.5em;
}

.menu {
    float: right;
    display: flex;
}

    .menu__item {
        font-weight: bold;
        color: white;
        display: inline-block;
        padding: .5em 1em;
        border-radius: .2em;
        border: 2px solid transparent;
        text-decoration: none;
        background: rgba(255, 255, 255, .1);
        transition: border-color .3s ease-in-out;
    }

    .menu__item--active, .menu__item:hover, .menu__item:focus {
        border-color: white;
        outline: none;
    }

    .menu__item + .menu__item {
        margin-left: 1em;
    }
.content {
    padding: 7em 2em 2em;
}
